// middleware.ts (프로젝트 루트)
// Next 16 + next-auth v5 패턴

import { auth } from "@/lib/auth"; // 너의 auth 설정 위치
import type { Session } from "next-auth";
import type { NextRequest } from "next/server";
import { NextResponse } from "next/server";

// next-auth v5의 auth 콜백에 들어오는 req에 .auth가 붙어있다고 가정
type RequestWithAuth = NextRequest & { auth: Session | null };

export default auth((req: RequestWithAuth) => {
  const { nextUrl } = req;

  // 보호할 경로만 먼저 최소로 잡자 (필요시 확장)
  const isProtected = nextUrl.pathname.startsWith("/admin");

  if (isProtected && !req.auth) {
    // 로그인 안됐는데 보호 경로 접근 → 로그인으로
    return NextResponse.redirect(new URL("/login", nextUrl));
  }

  // 통과
  return NextResponse.next();
});

// 어떤 경로에 미들웨어를 적용할지 명시
export const config = {
  matcher: ["/admin/:path*"],
};
